import {
  createApiRef,
  DiscoveryApi,
  OAuthApi,
} from '@backstage/core-plugin-api';

export type Rule = {
  title: string;
  code: string;
  type: string;
  url: string;
  is_active: boolean;
};

export type Violation = {
  title: string;
  description: string | string[];
  violation_type: string;
  rule_link: string;
  paths: string[];
  pointer: string;
  start_line: number;
  end_line: number;
};

export type ViolationCount = {
  must: number;
  should: number;
  may: number;
  hint: number;
};

export type ViolationsResponse = {
  external_id: string;
  message: string;
  violations: Violation[];
  violations_count: ViolationCount;
  api_definition: string;
};

export type ViolationRequest = {
  api_definition: {};
  api_definition_string: string;
  api_definition_url: string;
  ignore_rules: string[];
};

export type ViolationsByUrl = Pick<ViolationRequest, 'api_definition_url'>;
export type ViolationsByString = Pick<
  ViolationRequest,
  'api_definition_string'
>;

export type ZallyApiType = {
  getRules(): Promise<Rule[]>;
  getApiViolations(
    request: ViolationsByUrl | ViolationsByString,
  ): Promise<ViolationsResponse>;
  getSchemaAndViolations(id: string): Promise<ViolationsResponse>;
};

export const zallyApiRef = createApiRef<ZallyApiType>({
  id: 'plugin.api-linter',
});

interface Apis {
  discoveryApi: DiscoveryApi;
  oauth2Api: OAuthApi;
}

export class ZallyApi implements ZallyApiType {
  private readonly discoveryApi: DiscoveryApi;
  private readonly oauth2Api: OAuthApi;

  constructor(apis: Apis) {
    this.discoveryApi = apis.discoveryApi;
    this.oauth2Api = apis.oauth2Api;
  }

  async getRules(): Promise<Rule[]> {
    const { token, serviceUrl } = await this.urlToken('supported-rules');

    const response = await fetch(serviceUrl, {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
      credentials: 'include',
    });

    const data = await response.json();
    return data.rules;
  }

  async getApiViolations(
    request: ViolationsByUrl | ViolationsByString,
  ): Promise<ViolationsResponse> {
    const { token, serviceUrl } = await this.urlToken('api-violations');

    const response = await fetch(serviceUrl, {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
      credentials: 'include',
      method: 'POST',
      body: JSON.stringify(request),
    });

    if (!response.ok) {
      throw Error(response.statusText);
    }

    return response.json();
  }

  async getSchemaAndViolations(id: string) {
    const { token, serviceUrl } = await this.urlToken('api-violations');

    const response = await fetch(`${serviceUrl}/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
      credentials: 'include',
    });

    return response.json();
  }

  private async urlToken(path: string) {
    const token = await this.oauth2Api.getAccessToken();
    const proxyUrl = await this.discoveryApi.getBaseUrl('proxy');
    const serviceUrl = `${proxyUrl}/api-linter/${path}`;
    return { token, serviceUrl };
  }
}
