import {
  createApiFactory,
  createPlugin,
  discoveryApiRef,
  oauth2ApiRef,
} from '@backstage/core-plugin-api';

import { ZallyApi, zallyApiRef } from './api';

import { rootRouteRef } from './routes';

export const APILinterPlugin = createPlugin({
  id: 'api-linter',
  routes: {
    root: rootRouteRef,
  },
  apis: [
    createApiFactory({
      api: zallyApiRef,
      deps: { discoveryApi: discoveryApiRef, oauth2Api: oauth2ApiRef },
      factory: ({ discoveryApi, oauth2Api }) =>
        new ZallyApi({ discoveryApi, oauth2Api }),
    }),
  ],
});
